.carouselText{
   position:absolute;
   top:20%;
   left: 15%;
   height: 80%;
   text-align:center; 

   display: flex;
   flex-flow: column nowrap;
   justify-content: space-around;

}
.changingText{
  height: 20%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  gap:30px;
  
  text-align: start;
}
.changingText h1{
  font-size: 3.5rem;
  transition: all 0.1s ease;
  
 
}
.changingText p{
  font-weight: 300  ;
  font-size: 1.5rem;
  padding-left: 8px;
 
}
.button{
   /* width: 120px;
   height: 50px; */
   width: 120px;
   padding: 0.5rem 1rem;
   background-color: none;
   border:1px solid #fff;
   font-weight: 300;
   transition: all .3s ease;

}
.button:hover{
  cursor: pointer;
  color: rgba(0, 0, 0, 0.8);
  background-color: #fff;
  font-weight: 600;
  
  
}

.animation1{
  animation: anim1 0.9s;
}
.animation2{
  animation: anim2 0.9s;
}

@keyframes anim1{
  from{
    opacity: 0;
    margin-left: -100px;
  }
  to{
    opacity: 1;
   
  }
}
@keyframes anim2{
  from{
    opacity: 0;
    margin-left: -100px;
  }
  to{
    opacity: 1;
   
  }
}



